<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">订单详细数据</div>
      <div class="modal-body">
        <h3>基本信息</h3>
        <div class="row">
          <div class="columns">
            <span class="label">订单ID:</span>
            <span class="value">{{ form.order_id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">订单号:</span>
            <span class="value">{{ form.order_number }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">货件号:</span>
            <span class="value">{{ form.posting_number }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">运单号:</span>
            <span class="value">{{ form.tracking_number?form.tracking_number:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">系统状态:</span>
            <span class="value">{{ form.system_status?form.system_status:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">货运状态:</span>
            <span class="value">{{ form.posting_status?form.posting_status:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">物流状态:</span>
            <span class="value">{{ form.logistics_status?form.logistics_status:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">快递名称:</span>
            <span class="value">{{ form.delivery?form.delivery.delivery_name:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">快递服务类型:</span>
            <span class="value">{{ form.delivery?form.delivery.delivery_tpl_provider_type:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">快递服务名称:</span>
            <span class="value">{{ form.delivery?form.delivery.delivery_tpl_provider_name:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">仓库名称:</span>
            <span class="value">{{ form.warehouse_name?form.warehouse_name:"暂无" }}</span>
          </div>
        </div>


        <div class="row">
          <div class="columns">
            <span class="label">开始处理时间:</span>
            <span class="value">{{ in_process_at_time?in_process_at_time:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">必须收取时间:</span>
            <span class="value">{{ shipment_date?shipment_date:"暂无" }}</span>
          </div>
        </div>
        <div class="row">
          <div class="columns">
            <span class="label">审核时间:</span>
            <span class="value">{{ form.approval_time?form.approval_time:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">采购时间:</span>
            <span class="value">{{ form.dispatch_time?form.dispatch_time:"暂无" }}</span>
          </div>
        </div>
        <div class="row">
          <div class="columns">
            <span class="label">发货时间:</span>
            <span class="value">{{ form.shipping_time?form.shipping_time:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">作废时间:</span>
            <span class="value">{{ form.cancel_time?form.cancel_time:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">订单总价:</span>
            <span class="value">{{ form.total_price }}</span>
          </div>
          <div class="columns">
            <span class="label">价格货币:</span>
            <span class="value">{{ form.currency_code }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">店铺名称:</span>
            <span class="value">{{ form.shop?form.shop.name:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">负责人员:</span>
            <span class="value">{{ form.owner?form.owner.name:"暂无" }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">取消原因:</span>
            <span class="value">{{ form.cancel_reason?form.cancel_reason:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">取消类型:</span>
            <span class="value">{{ form.cancellation_type?form.cancellation_type:"暂无" }}</span>
          </div>
        </div>
        
        <h3 style="margin-top: 10px;">ozon产品信息</h3>
        <div v-for="(product, index) in form.products" :key="product.id" style="display: flex; align-items: center;">
          <span style="margin-right: 8px;">{{ index+1 }}.</span>
          <!-- 订单中的ozon商品信息 -->
          <div style="width: 35%;">
            <div class="row">
              <div class="columns">
                <img :src="product.primary_image" style="width: 50px; height: 50px;" alt="产品主图" />
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">产品名称:</span>
                <span class="value">{{ product.name }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">offer_id:</span>
                <span class="value">{{ product.offer_id }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">sku:</span>
                <span class="value">{{ product.sku }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">product_id:</span>
                <span class="value">{{ product.product_id }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">产品价格:</span>
                <span class="value">{{ product.price }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">货币单位:</span>
                <span class="value">{{ product.currency_code }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">产品数量:</span>
                <span class="value">{{ product.quantity }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">fbo佣金百分比:</span>
                <span class="value">{{ product.fbo_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbo佣金额:</span>
                <span class="value">{{ product.fbo_commission_value }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">fbp佣金百分比:</span>
                <span class="value">{{ product.fbp_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbp佣金额:</span>
                <span class="value">{{ product.fbp_commission_value }}</span>
              </div>
            </div>

            <div class="row">

              <div class="columns">
                <span class="label">fbs佣金百分比:</span>
                <span class="value">{{ product.fbs_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">fbs佣金额:</span>
                <span class="value">{{ product.fbs_commission_value }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">rfbs佣金百分比:</span>
                <span class="value">{{ product.rfbs_commission_percent }}%</span>
              </div>
              <div class="columns">
                <span class="label">rfbs佣金额:</span>
                <span class="value">{{ product.rfbs_commission_value }}</span>
              </div>
            </div>
          </div>

          <div>
            <span>
              ==>
            </span>
          </div>

          <!-- 产品绑定的系统内商品信息 -->
          <div>
            <div v-for="(system_product,index) in product.system_products" :key="system_product.id" style="display: flex;">
              <span style="margin-right: 8px;">{{ index+1 }}.</span>
              <div>
                <div class="row">
                  <div class="columns">
                    <img :src="system_product.primary_image" style="width: 50px; height: 50px;" alt="产品主图" />
                  </div>
                </div>
                
                <div class="row">
                  <div class="columns">
                    <span class="label">id:</span>
                    <span class="value">{{ system_product.id }}</span>
                  </div>
                </div>

                <div class="row">
                  <div class="columns">
                    <span class="label">绑定数量:</span>
                    <span class="value">{{ system_product.quantity }}</span>
                  </div>
                </div>

                <div class="row">
                  <div class="columns">
                    <span class="label">system_sku:</span>
                    <span class="value">{{ system_product.system_sku }}</span>
                  </div>
                </div>

                <div class="row">
                  <div class="columns">
                    <span class="label">参考重量:</span>
                    <span class="value">{{ system_product.reference_weight }}</span>
                  </div>
                  <div class="columns">
                    <span class="label">参考价格:</span>
                    <span class="value">{{ system_product.reference_cost }}</span>
                  </div>
                </div>

                <div class="row">
                  <div class="columns" style="display: flex; align-items: center;">
                    <span class="label" style="width: 71px;">采购链接:</span>
                    <div>
                      <div v-for="link,index in JSON.parse(system_product.purchase_link)" :key="link+index">
                        <span>{{ index+1 }}：</span>
                        <a :href="link.link" target="_blank"> {{ `链接${index+1}号` }} </a>
                        <span> {{ link.msg }} </span>
                      </div>
                    </div>
                  </div>
                </div>

                <div class="row">
                  <div class="columns">
                    <span class="label">供应商名称:</span>
                    <span class="value">{{ system_product.supplier_name?system_product.supplier_name:"暂无" }}</span>
                  </div>
                  <div class="columns">
                    <span class="label">采购平台:</span>
                    <span class="value">{{ system_product.purchase_platform }}</span>
                  </div>
                </div>

                <div class="row">
                  <div class="columns">
                    <span class="label">待采购量:</span>
                    <span class="value">{{ system_product.wait_for_purchase_quantity }}</span>
                  </div>
                  <div class="columns">
                    <span class="label">在篮量:</span>
                    <span class="value">{{ system_product.in_basket_quantity }}</span>
                  </div>
                  <div class="columns">
                    <span class="label">在途量:</span>
                    <span class="value">{{ system_product.in_transit_quantity }}</span>
                  </div>
                </div>

                <div class="row">
                  <div class="columns">
                    <span class="label">库存量:</span>
                    <span class="value">{{ system_product.stock_quantity }}</span>
                  </div>
                  <div class="columns">
                    <span class="label">出库量:</span>
                    <span class="value">{{ system_product.out_stock_quantity }}</span>
                  </div>
                  <div class="columns">
                    <span class="label">丢失量:</span>
                    <span class="value">{{ system_product.loss_quantity }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <h3 style="margin-top: 10px;">采购商品信息</h3>
        <div v-for="(purchase_product, index) in form.purchase_products" :key="purchase_product.id" style="display: flex; align-items: center;">
          <span style="margin-right: 8px;">{{ index+1 }}.</span>
          <div style="margin-top:5px; border:1px solid black;">
            <div class="row">
              <div class="columns">
                <span class="label">采购商品ID:</span>
                <span class="value">{{ purchase_product.id }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">采购商品sku:</span>
                <span class="value">{{ purchase_product.sku }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">采购价格:</span>
                <span class="value">{{ purchase_product.price }}</span>
              </div>
              
              <div class="columns">
                <span class="label">采购类型:</span>
                <span class="value">{{ purchase_product.type }}</span>
              </div>

              <div class="columns">
                <span class="label">采购状态:</span>
                <span class="value">{{ purchase_product.status }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">创建时间:</span>
                <span class="value">{{ purchase_product.create_time }}</span>
              </div>

              <div class="columns">
                <span class="label">入库时间:</span>
                <span class="value">{{ purchase_product.stock_in_date }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">采购单ID:</span>
                <span class="value">{{ purchase_product.purchase_order_id }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">系统内商品ID:</span>
                <span class="value">{{ purchase_product.system_product_id }}</span>
              </div>
            </div>

          </div>
        </div>
    </div>
    </a-modal>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  name: 'FormModal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    in_process_at_time(){
      return moment(this.form.in_process_at).format('YYYY-MM-DD HH:mm:ss')
    },
    shipment_date(){
      return moment(this.form.shipment_date).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  methods: {
    confirm() {
      this.$emit('cancel', false);
    },
    cancel() {
      this.$emit('cancel', false);
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns .label{
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}
</style>